import React from 'react';
import MutualFund from 'components/mutual-fund';

import postStyles from 'styles/post.module.scss';
import styles from './style.module.scss';

class Playground extends React.Component {

    constructor(props){
        super(props);
        this.sidePocketFunds = [
            {
                fund: 'Motilal Oswal Nasdaq 100 FOF'
            },
            {
                fund: 'Motilal Oswal Nasdaq 100 FOF'
            },
            {
                fund: 'Motilal Oswal Nasdaq 100 FOF'
            },
            {
                fund: 'Motilal Oswal Nasdaq 100 FOF'
            },
            {
                fund: 'Motilal Oswal Nasdaq 100 FOF'
            },
            {
                fund: 'Motilal Oswal Nasdaq 100 FOF'
            },
        ]
        this.midFunds = [
            {
                fund: 'Motilal Oswal Nasdaq 100 FOF'
            },
            {
                fund: 'Motilal Oswal Nasdaq 100 FOF'
            },
            {
                fund: 'Motilal Oswal Nasdaq 100 FOF'
            },
            {
                fund: 'Motilal Oswal Nasdaq 100 FOF'
            },
            {
                fund: 'Motilal Oswal Nasdaq 100 FOF'
            },
            {
                fund: 'Motilal Oswal Nasdaq 100 FOF'
            },
        ]
        this.state = {
            page: 1,
            sideFunds: [],
            midFunds: [],
        };
    }

    next = ()=>{
        console.log("click")
        if(this.state.page!=5){
            console.log("click");
            var page = this.state.page+1;
            this.setState({page: page});
        }
    }

    selectSideFund=(i)=>()=>{
        var index = this.state.sideFunds.indexOf(i);
        if(index==-1){
            this.setState({sideFunds: [...this.state.sideFunds, i]});
        }
        else{
            console.log(this.state.sideFunds);
            console.log(index);
            var selected = [...this.state.sideFunds];
            selected.splice(index,1);
            console.log(selected);
            this.setState({sideFunds: selected});
        }
    }

    selectMidFund=(i)=>()=>{
        var index = this.state.midFunds.indexOf(i);
        if(index==-1){
            this.setState({midFunds: [...this.state.midFunds, i]});
        }
        else{
            console.log(this.state.midFunds);
            console.log(index);
            var selected = [...this.state.midFunds];
            selected.splice(index,1);
            console.log(selected);
            this.setState({midFunds: selected});
        }
    }

    render (){
        return(
            <div className={postStyles["post"]}>
                <div className={postStyles["header"]}>
                  <div className={postStyles["title-box"]}>
                    <div className={postStyles["container"]}>
                      <h3 className={postStyles["title"]}>The Playground</h3>
                      <div className={postStyles["title-line"]}></div>
                    </div>
                  </div>
                </div>
                <div className={postStyles["post-content"]}>
                    {this.state.page==1 && (<div>
                        <p>Now that you know everything you need to know about mutual funds, itâ€™s time to put your skills to use and mix up a kickass portfolio.</p>
                        <p style={{textAlign: "center"}}>
                            <span className={styles["get-started"]} onClick={()=>{this.setState({page: 2})}}>GET STARTED</span>
                        </p>
                    </div>)}
                    {this.state.page==2 && (<div>
                        <p>Pick the funds you want to include in your :</p>
                        <h4>Side Pocket</h4>
                        <p>(All your spare cash goes here)</p>
                        <br/>
                        <div style={{width: "95%", margin: "auto"}}>
                            {this.sidePocketFunds.map((x,i)=>(<MutualFund selected={this.state.sideFunds.indexOf(i)!=-1} onClick={this.selectSideFund(i)}/>))}
                        </div>
                    </div>)}
                    {this.state.page==3 && (<div>
                        <p>Pick the funds you want in your portfolio if you were staying invested for :</p>
                        <h4>1 to 3 years</h4>
                        <div style={{width: "95%", margin: "auto"}}>
                            {this.midFunds.map((x,i)=>(<MutualFund selected={this.state.midFunds.indexOf(i)!=-1} onClick={this.selectMidFund(i)}/>))}
                        </div>
                    </div>)}
                </div>
                <div className={styles["nav"]}>
                    <div className={styles["back"]} onClick={this.back}>BACK</div>
                    <div className={styles["next"]} onClick={this.next}>NEXT TENURE</div>
                </div>
            </div>
        )
    }
}

export default Playground;